import {
  setAllView,
  setOneStopView,
  setTwoStopsView,
  setThreeStopsView,
  setNoStopsView,
} from '../redux/reducers'

export const checkbox = [
  { id: 'cb1', value: '', reducer: setAllView },
  { id: 'cb0', value: ' ', reducer: setNoStopsView },
  { id: 'cb2', value: '1 ', reducer: setOneStopView },
  { id: 'cb3', value: '2 ', reducer: setTwoStopsView },
  { id: 'cb4', value: '3 ', reducer: setThreeStopsView },
]



